html {
  min-height: 100vh;
  overflow-x: hidden;
  background: #22222c;
  color: #eefbff;
  font-family: 'Play', sans-serif;
}

.App {
  position: relative;
  overflow-x: hidden;
}

html::-webkit-scrollbar {
  width: 1em;
}
 
html::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
html::-webkit-scrollbar-thumb {
  background-color: #eefbff;
  outline: 1px solid #6365b0;
}